/*!
 * Import what components you need, use [babel-plugin-import](https://github.com/ant-design/babel-plugin-import)
 * bqliu
 */

import Vue from 'vue'
import {
  // Badge,
  Button,
  Breadcrumb,
  Select,
  // Calendar,
  Card,
  // Checkbox,
  Form,
  Icon,
  Input,
  Layout,
  LocaleProvider,
  Menu,
  message,
  notification,
  Pagination,
  // Radio,
  Table,
  // Tabs,
  // Tag,
  Tooltip,
  Modal,
  Spin
} from 'ant-design-vue'

;[
  // Badge,
  Button,
  Breadcrumb,
  // Calendar,
  Card,
  // Checkbox,
  Select,
  Form,
  Icon,
  Input,
  Layout,
  LocaleProvider,
  Menu,
  Pagination,
  // Radio,
  Table,
  // Tabs,
  // Tag,
  Tooltip,
  Modal,
  Spin
].forEach((component) => {
  Vue.use(component)
})

Vue.prototype.$message = message
Vue.prototype.$notification = notification
Vue.prototype.$info = Modal.info
Vue.prototype.$success = Modal.success
Vue.prototype.$error = Modal.error
Vue.prototype.$warning = Modal.warning
Vue.prototype.$confirm = Modal.confirm
